import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';

import { HttpClientModule, HttpClient } from '@angular/common/http';
import { TranslateModule, TranslateLoader } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { AccountPage } from '../pages/account/account';
import { AddreviewPage } from '../pages/addreview/addreview';
import { ChatsPage } from '../pages/chats/chats';
import { ChattingPage } from '../pages/chatting/chatting';
import { ConditionPage } from '../pages/condition/condition';
import { Edit_offerPage } from '../pages/edit_offer/edit_offer';
import { Edit_eventPage } from '../pages/edit_event/edit_event';
import { EventdetailPage } from '../pages/eventdetail/eventdetail';
import { HelpPage } from '../pages/help/help';
import { HomePage } from '../pages/home/home';
import { MapPage } from '../pages/map/map';
import { OfferdetailPage } from '../pages/offerdetail/offerdetail';
import { PasswordPage } from '../pages/password/password';
import { ProfilePage } from '../pages/profile/profile';
import { RegisterPage } from '../pages/register/register';
import { ReviewPage } from '../pages/review/review';
import { SearchPage } from '../pages/search/search';
import { SigninPage } from '../pages/signin/signin';
import { TabsPage } from '../pages/tabs/tabs';
import { TestPage } from '../pages/test/test';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

export function createTranslateLoader(http: HttpClient) {
  return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}

@NgModule({
  declarations: [
    MyApp,
    AccountPage,
    AddreviewPage,
    ChatsPage,
    ChattingPage,
    ConditionPage,
    Edit_offerPage,
    Edit_eventPage,
    EventdetailPage,
    HelpPage,
    HomePage,
    MapPage,
    OfferdetailPage,
    PasswordPage,
    ProfilePage,
    RegisterPage,
    ReviewPage,
    SearchPage,
    SigninPage,
    TabsPage,
    TestPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
    HttpClientModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: createTranslateLoader,
        deps: [HttpClient]
      }
    })
  ], 
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    AccountPage,
    AddreviewPage,
    ChatsPage,
    ChattingPage,
    ConditionPage,
    Edit_offerPage,
    Edit_eventPage,
    EventdetailPage,
    HelpPage,
    HomePage,
    MapPage,
    OfferdetailPage,
    PasswordPage,
    ProfilePage,
    RegisterPage,
    ReviewPage,
    SearchPage,
    SigninPage,
    TabsPage,
    TestPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



